.card {
    display: grid;
    margin: 10px 10px;
    padding: 10px;
    box-shadow: 1px 1px 10px grey;
    width: 250px;
    height: 300px;
    border-radius: 15px;
}

.card-img {
    width: 250px;
    border-radius: 15px;
    height: 150px;
    box-shadow: 1px 1px 10px grey;
}

#btn {
    border: none;
    outline: none;
    background-color: rgb(0, 0, 0);
    color: white;
    font-size: 15px;
    border-radius: 10px;
    cursor: pointer;
}
