@media only screen and (max-width: 400px) {
    .container-cards {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .card {
        display: grid;
        margin: 10px 10px;
        padding: 10px;
        box-shadow: 1px 1px 10px grey;
        width: 300px;
        height: 350px;
        border-radius: 15px;
    }

